<template>
    <!--  -->
    <section id="operation">
        <div id="account">
            <span class="element">{{ ROLE_VALUE[$store.state.role] }}
                {{ USER_VALUE[$store.state.user] }}</span>
        </div>
        <!--  -->
        <div id="search">
            <el-input v-model.trim="search" class="w-50 m-2"
                :placeholder="this.$store.state.plant == 'assy' ? '' : ''" @keyup.enter="handleSearch">
                <template #prefix>
                    <el-icon class="el-input__icon">
                        <search />
                    </el-icon>
                </template>
            </el-input>
        </div>
        <div id="switch">
            <el-switch ref="switch" v-model="switch_value" active-text="" :disabled="$store.state.role != 'root'" />
        </div>

        <!--  -->
        <div id="info" v-show="!switch_value">
            <el-descriptions title="" :column="1" border>
                <el-descriptions-item label="" label-align="right" align="center" width="100px">{{ formMsg.deviceNum
                }}</el-descriptions-item>
                <el-descriptions-item label="" label-align="right" align="center" width="100px">{{ formMsg.stationNum
                }}</el-descriptions-item>
            </el-descriptions>
            <el-descriptions title="" :column="1" border>
                <el-descriptions-item label="" label-align="right" align="center" width="100px">
                    <el-tag size="small" type="info">{{ formMsg.problem.count }}</el-tag>
                </el-descriptions-item>
                <el-descriptions-item label="" label-align="right" align="center" width="100px">
                    <el-tag size="small" type="danger">{{ formMsg.problem.needHelpAndUnfinished }}</el-tag>
                    <span>&nbsp;</span>
                    <el-tag size="small" type="warning">{{ formMsg.problem.needHelpAndReview }}</el-tag>
                    <span>&nbsp;</span>
                    <el-tag size="small" type="success">{{ formMsg.problem.needHelpAndfinished }}</el-tag>
                </el-descriptions-item>
                <el-descriptions-item label="" label-align="right" align="center" width="100px">
                    <el-tag size="small" type="danger">{{ formMsg.problem.noHelpAndUnfinished }}</el-tag>
                    <span>&nbsp;</span>
                    <el-tag size="small" type="success">{{ formMsg.problem.noHelpAndfinished }}</el-tag>
                </el-descriptions-item>
                <el-descriptions-item label="" label-align="right" align="center" width="100px">
                    <el-tag size="small">{{ (formMsg.problem.count != 0) ?
                        (((formMsg.problem.needHelpAndfinished + formMsg.problem.noHelpAndfinished) /
                            formMsg.problem.count) * 100).toFixed(0) : 0 }}%</el-tag>
                </el-descriptions-item>
            </el-descriptions>

            <el-link :underline="false" type="primary" id="link" @click="toProblems">>></el-link>
        </div>

        <!--  -->
        <div id="edit_info" v-show="switch_value">
            <el-form label-width="80px" :model="formLabel" :rules="rules" ref="form">
                <el-form-item label="" prop="deviceNum">
                    <el-input v-model="formLabel.deviceNum" />
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="formLabel.stationNum" />
                </el-form-item>
                <el-form-item label="X" prop="coordX">
                    <el-input v-model="formLabel.coordX" />
                </el-form-item>
                <el-form-item label="Y" prop="coordY">
                    <el-input v-model="formLabel.coordY" />
                </el-form-item>
                <el-form-item label="" prop="width">
                    <el-input v-model="formLabel.width" />
                </el-form-item>
                <el-form-item label="" prop="height">
                    <el-input v-model="formLabel.height" />
                </el-form-item>
                <el-form-item label="" prop="conveyor">
                    <el-radio v-model="formLabel.conveyor" label='true'></el-radio>
                    <el-radio v-model="formLabel.conveyor" label='false'></el-radio>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="saveMachine('form')"></el-button>
                    <el-button type="danger" @click="deleteMachine('form')"></el-button>
                </el-form-item>
            </el-form>
        </div>

        <div id="map-button">
            <el-select class="displayFilter" v-model="$store.state.displayByUser">
                <el-option v-for="item in $store.state.displayOptions" :key="item.value" :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select>

            <button @click="bigger()">+</button>
            <span></span>
            <button @click="smaller()">-</button>
            <span>&nbsp;&nbsp;{{ map.per }}%</span>
        </div>
        <!--  -->
        <canvas id='canvas' ref="thumbnailDom" width="203" height="133" @mousedown="startDraging" @mouseup="stopDraging"
            @mousemove="draging">
        </canvas>
    </section>
</template>

<script>
import { Search } from '@element-plus/icons-vue'
import { mapState } from 'vuex'

export default {
    name: 'Operation',
    components: {
        Search
    },
    data() {
        return {
            // 
            search: "",
            // 
            switch_value: false,
            // 
            formMsg: {
                // 
                deviceNum: "",
                // 
                stationNum: "",
                // 
                problem: {
                    // 
                    count: 0,
                    //  
                    needHelpAndUnfinished: 0,
                    //  
                    needHelpAndReview: 0,
                    //  
                    needHelpAndfinished: 0,
                    //  
                    noHelpAndUnfinished: 0,
                    //  
                    noHelpAndfinished: 0
                }
            },
            // 
            formLabel: {
                // 
                deviceNum: "",
                // 
                stationNum: "",
                // X
                coordX: "",
                // Y
                coordY: "",
                // 
                width: "",
                // 
                height: "",
                // 
                conveyor: ""
            },
            // 
            rectLeft: 0,
            // 
            rectTop: 0,
            // 
            firstClickX: 0,
            firstClickY: 0,
            rules: {
                deviceNum: [
                    { required: true, message: '', trigger: 'submit' }
                ],
                coordX: [
                    { required: true, message: 'X', trigger: 'submit' }
                ],
                coordY: [
                    { required: true, message: 'Y', trigger: 'submit' }
                ],
                width: [
                    { required: true, message: '', trigger: 'submit' }
                ],
                height: [
                    { required: true, message: '', trigger: 'submit' }
                ],
                conveyor: [
                    { required: true, message: '', trigger: 'submit' }
                ]
            },
            ROLE_VALUE: {
                'root': '',
                'assist': '',
                'local': ''
            },
            USER_VALUE: {
                'root': 'root',
                'zt1': 'ZT1-',
                'zt2': 'ZT2-',
                'zt3': 'ZT3-',
                'improve': '',
                'provide': '',
                'assy': '',
                'logistics': '',
                'case': '',
                'gear': '',
                'pulley': '',
                'differential': '',
                'heat': ''
            }
        }
    },
    computed: {
        ...mapState(['map', 'thumbnail'])
    },
    methods: {
        // 
        bigger() {
            if (this.map.per < 100) {
                this.map.width += 10 * ((this.map.maxWidth - this.map.minWidth) / 100);
                this.map.height += 10 * ((this.map.maxHeight - this.map.minHeight) / 100);
                this.map.per += 10;
                this.$emit('init');
            }
        },
        // 
        smaller() {
            if (this.map.per > 0) {
                this.map.width -= 10 * ((this.map.maxWidth - this.map.minWidth) / 100);
                this.map.height -= 10 * ((this.map.maxHeight - this.map.minHeight) / 100);
                this.map.per -= 10;
                this.$emit('init');
            }
        },
        // 
        drawThumbnail(shape, key) {
            const thumbnailContext = this.$refs['thumbnailDom'].getContext("2d");
            thumbnailContext.beginPath();
            thumbnailContext.strokeStyle = "#696969";
            thumbnailContext.rect(
                shape["coordX"] * 0.35,
                shape["coordY"] * 0.35,
                shape["width"] * 0.35,
                shape["height"] * 0.35
            );
            /**
             * 
             * 
             *  
             */
            if (shape['unfinishedCount'] > 0) {
                thumbnailContext.fillStyle = "red";
                thumbnailContext.fill();
                thumbnailContext.stroke();
            } else if (shape['reviewCount']) {
                thumbnailContext.fillStyle = "yellow";
                thumbnailContext.fill();
                thumbnailContext.stroke();
            } else {
                //
                if (shape["conveyor"] == "true") {
                    thumbnailContext.fillStyle = "black";
                    thumbnailContext.fill();
                    thumbnailContext.stroke();
                } else {
                    thumbnailContext.fillStyle = "#B5B5B5";
                    thumbnailContext.fill();
                    thumbnailContext.stroke();
                }
            }
        },
        // 
        drawChoose() {
            const shape = this.$store.state.shapes.get(this.$store.state.choose);
            const thumbnailContext = this.$refs['thumbnailDom'].getContext("2d");
            // 
            if (shape != null) {
                thumbnailContext.beginPath();
                thumbnailContext.rect(
                    shape["coordX"] * 0.35,
                    shape["coordY"] * 0.35,
                    shape["width"] * 0.35,
                    shape["height"] * 0.35
                );
                thumbnailContext.fillStyle = "#00ffff";
                thumbnailContext.fill();
            }
        },
        // 
        drawThumbnailCheck(widthPer, heightPer, offsetX, offsetY) {
            const thumbnailContext = this.$refs['thumbnailDom'].getContext("2d");
            // 
            this.thumbnail.checkOffsetX = this.thumbnail.width * offsetX;
            this.thumbnail.checkOffsetY = this.thumbnail.height * offsetY;
            this.thumbnail.checkWidth = this.thumbnail.width * ((widthPer > 1) ? 1 : widthPer);
            this.thumbnail.checkHeight = this.thumbnail.height * ((heightPer > 1) ? 1 : heightPer);
            // 
            thumbnailContext.strokeStyle = "#00ffff";
            thumbnailContext.beginPath();
            thumbnailContext.lineWidth = 2;
            thumbnailContext.rect(
                this.thumbnail.checkOffsetX,
                this.thumbnail.checkOffsetY,
                this.thumbnail.checkWidth,
                this.thumbnail.checkHeight
            );
            thumbnailContext.stroke();
            thumbnailContext.lineWidth = 1;
        },
        // 
        startDraging(event) {
            const rect = this.$refs['thumbnailDom'].getBoundingClientRect();
            this.rectLeft = rect.left;
            this.rectTop = rect.top;
            this.firstClickX = event.clientX - this.rectLeft;
            this.firstClickY = event.clientY - this.rectTop;
            console.log('start draging...');
        },
        // 
        stopDraging() {
            this.rectLeft = 0;
            this.rectTop = 0;
            this.firstClickX = 0;
            this.firstClickY = 0;
            this.$emit('init');
            console.log('over drag');
        },
        // 
        draging(event) {
            // 00
            if (this.rectLeft > 0 && this.rectTop > 0) {
                // x, y 
                const x = event.clientX - this.rectLeft;
                const y = event.clientY - this.rectTop;
                // (x - this.firstClickX) 
                // offsetXoffsetY   
                const offsetX = ((x - this.firstClickX) / this.thumbnail.width) * this.map.width;
                const offsetY = ((y - this.firstClickY) / this.thumbnail.height) * this.map.height;
                //  +  = 
                this.$emit('onDraging', this.map.scrollLeft + offsetX, this.map.scrollTop + offsetY);
            }
        },
        updateForm() {
            const shape = this.$store.state.shapes.get(this.$store.state.choose);
            if (shape == null) {
                this.formMsg.deviceNum = '';
                this.formMsg.stationNum = '';
                this.formLabel.deviceNum = '';
                this.formLabel.stationNum = '';
                this.formLabel.coordX = '';
                this.formLabel.coordY = '';
                this.formLabel.width = '';
                this.formLabel.height = '';
                this.formLabel.conveyor = '';
            } else {
                this.formMsg.deviceNum = shape['deviceNum'];
                this.formMsg.stationNum = shape['stationNum'];
                this.formLabel.deviceNum = shape['deviceNum'];
                this.formLabel.stationNum = shape['stationNum'];
                this.formLabel.coordX = shape['coordX'];
                this.formLabel.coordY = shape['coordY'];
                this.formLabel.width = shape['width'];
                this.formLabel.height = shape['height'];
                this.formLabel.conveyor = shape['conveyor'];
            }
        },
        // /
        saveMachine(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    if (this.$store.state.choose == '') {
                        this.$confirm('', '', {
                            confirmButtonText: '',
                            confirmButtonClass: 'greenClass',
                            cancelButtonText: '',
                            type: 'warning'
                        }).then(() => {
                            //  + 
                            if (this.$store.state.shapes.has(this.formLabel.deviceNum + '+' + this.formLabel.stationNum)) {
                                this.$message({
                                    showClose: true,
                                    message: ' + ',
                                    type: 'error'
                                });
                            }
                            else if (this.verifyForm()) {
                                this.$store.state.shapes.set(this.$store.state.choose, {
                                    deviceNum: this.formLabel.deviceNum,
                                    stationNum: this.formLabel.stationNum,
                                    coordX: this.formLabel.coordX,
                                    coordY: this.formLabel.coordY,
                                    width: this.formLabel.width,
                                    height: this.formLabel.height,
                                    conveyor: this.formLabel.conveyor
                                });
                                this.updatePlantData().then(data => {
                                    this.$store.state.choose = this.formLabel.deviceNum + '+' + this.formLabel.stationNum;
                                    this.$message({
                                        showClose: true,
                                        message: '',
                                        type: 'success'
                                    });
                                    this.$store.state.choose = this.formLabel.deviceNum + '+' + this.formLabel.stationNum;
                                })
                            }
                        }).catch(() => {
                            this.$message({
                                type: 'info',
                                message: ''
                            });
                        });
                    } else {
                        this.$confirm('', '', {
                            confirmButtonText: '',
                            confirmButtonClass: 'greenClass',
                            cancelButtonText: '',
                            type: 'warning'
                        }).then(() => {
                            if (this.verifyForm()) {
                                // choosedisMap.vue
                                // 
                                this.$store.state.shapes.set(this.$store.state.choose, {
                                    deviceNum: this.formLabel.deviceNum,
                                    stationNum: this.formLabel.stationNum,
                                    coordX: this.formLabel.coordX,
                                    coordY: this.formLabel.coordY,
                                    width: this.formLabel.width,
                                    height: this.formLabel.height,
                                    conveyor: this.formLabel.conveyor
                                });
                                this.updatePlantData().then(data => {
                                    this.$message({
                                        showClose: true,
                                        message: '',
                                        type: 'success'
                                    });
                                    this.$emit('init');
                                })
                            }
                        }).catch(() => {
                            this.$message({
                                type: 'info',
                                message: ''
                            });
                        });
                    }
                }
            });
        },
        // 
        deleteMachine(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    if (this.$store.state.choose == '') {
                        this.$message({
                            showClose: true,
                            message: '',
                            type: 'error'
                        });
                        this.formMsg.deviceNum = '';
                        this.formMsg.stationNum = '';
                        this.formLabel.deviceNum = '';
                        this.formLabel.stationNum = '';
                        this.formLabel.coordX = '';
                        this.formLabel.coordY = '';
                        this.formLabel.width = '';
                        this.formLabel.height = '';
                        this.formLabel.conveyor = '';
                    } else {
                        this.$confirm('', '', {
                            confirmButtonText: '',
                            confirmButtonClass: 'redClass',
                            cancelButtonText: '',
                            type: 'warning'
                        }).then(() => {
                            this.$store.state.shapes.delete(this.$store.state.choose);
                            this.updatePlantData().then(data => {
                                this.$message({
                                    showClose: true,
                                    message: '',
                                    type: 'success'
                                });
                                this.$store.state.choose = '';
                                this.formMsg.deviceNum = '';
                                this.formMsg.stationNum = '';
                                this.formLabel.deviceNum = '';
                                this.formLabel.stationNum = '';
                                this.formLabel.coordX = '';
                                this.formLabel.coordY = '';
                                this.formLabel.width = '';
                                this.formLabel.height = '';
                                this.formLabel.conveyor = '';
                            })
                        }).catch(() => {
                            this.$message({
                                type: 'info',
                                message: ''
                            });
                        });
                    }
                }
            });
        },
        // 
        updatePlantData() {
            const that = this;
            var shapes = this.$store.state.shapes;
            // reviewCountunfinishedCount
            for (let [key, value] of shapes) {
                delete value.reviewCount;
                delete value.unfinishedCount;
            }
            return new Promise((resolve, reject) => {
                this.$axiosInstance.post("/plant", JSON.parse(JSON.stringify(Array.from(shapes.values()))), {
                    params: {
                        name: this.$store.state.plant
                    }
                }).then(function (response) {
                    console.log('');
                    resolve(response);
                }).catch(function (error) {
                    that.$message({
                        showClose: true,
                        message: '',
                        type: 'error'
                    });
                    reject(error);
                })
            })
        },
        // 
        handleSearch() {
            if (this.$store.state.plant == 'assy') {
                console.log(`: ${this.search}`);
                // key = value = +
                const values = Array.from(this.$store.state.shapes.keys()).reduce((acc, cur, index) => {
                    acc[cur.split('+').pop()] = cur;
                    return acc;
                }, {});
                // 
                if (values[this.search] != null) {
                    this.$message({
                        showClose: true,
                        message: '',
                        type: 'success'
                    });
                    this.$store.state.choose = values[this.search];
                } else {
                    this.$message({
                        showClose: true,
                        message: '',
                        type: 'warning'
                    });
                    this.$store.state.choose = '';
                    this.updateForm();
                }
            } else {
                console.log(`: ${this.search}`);
                // key = +value = 
                // value
                const values = Array.from(this.$store.state.shapes.keys()).reduce((acc, cur, index) => {
                    const arr = cur.split('+');
                    arr.pop();
                    acc[cur] = arr[0];
                    return acc;
                }, {});
                // valuekey
                var flag = false;
                for (let key in values) {
                    if (values[key] == this.search) {
                        this.$store.state.choose = key;
                        flag = true;
                        break;
                    }
                }
                if (flag) {
                    this.$message({
                        showClose: true,
                        message: '',
                        type: 'success'
                    });
                } else {
                    this.$message({
                        showClose: true,
                        message: '',
                        type: 'warning'
                    });
                    this.$store.state.choose = '';
                }
            }
        },
        // 
        setProblemCount() {
            var deviceNum = null;
            var stationNum = null;
            if (!this.isEmpty(this.$store.state.choose)) {
                const arr = this.$store.state.choose.split('+');
                deviceNum = arr[0];
                stationNum = arr[1];
            }
            this.execCountRequest(deviceNum, stationNum, null, null).then(data => {
                this.formMsg.problem.count = data;
            })
            this.execCountRequest(deviceNum, stationNum, true, 'unfinished').then(data => {
                this.formMsg.problem.needHelpAndUnfinished = data;
            })
            this.execCountRequest(deviceNum, stationNum, true, 'review').then(data => {
                this.formMsg.problem.needHelpAndReview = data;
            })
            this.execCountRequest(deviceNum, stationNum, true, 'finished').then(data => {
                this.formMsg.problem.needHelpAndfinished = data;
            })
            this.execCountRequest(deviceNum, stationNum, false, 'unfinished').then(data => {
                this.formMsg.problem.noHelpAndUnfinished = data;
            })
            this.execCountRequest(deviceNum, stationNum, false, 'finished').then(data => {
                this.formMsg.problem.noHelpAndfinished = data;
            })
        },
        // countsetProblemCount()
        execCountRequest(deviceNum, stationNum, isNeedHelp, status) {
            return new Promise((resolve, reject) => {
                this.$axiosInstance.get("/plant/problems/count", {
                    params: {
                        plant: this.$store.state.plant,
                        deviceNum: deviceNum,
                        stationNum: stationNum,
                        isNeedHelp: isNeedHelp,
                        status: status
                    }
                }).then(function (response) {
                    resolve(response.data['count']);
                }).catch(function (error) {
                    reject(error);
                })
            })
        },
        // 
        toProblems() {
            // 
            if (!this.isEmpty(this.$store.state.choose)) {
                this.$emit('removeResizeEvent');
                this.$router.push(this.$route.path + '/problems');
            } else {
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'warning'
                });
            }
        },
        // 
        verifyForm() {
            // XYwidthheight
            if (!this.isNumeric(this.formLabel.coordX)) {
                this.$message({
                    showClose: true,
                    message: 'X',
                    type: 'error'
                });
                return false;
            }
            if (!this.isNumeric(this.formLabel.coordY)) {
                this.$message({
                    showClose: true,
                    message: 'Y',
                    type: 'error'
                });
                return false;
            }
            if (!this.isNumeric(this.formLabel.width)) {
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });
                return false;
            }
            if (!this.isNumeric(this.formLabel.height)) {
                this.$message({
                    showClose: true,
                    message: '',
                    type: 'error'
                });
                return false;
            }
            // x+width580y+height380
            if ((parseFloat(this.formLabel.coordX) + parseFloat(this.formLabel.width)) > 580) {
                this.$message({
                    showClose: true,
                    message: 'X + 580',
                    type: 'error'
                });
                return false;
            }
            if ((parseFloat(this.formLabel.coordY) + parseFloat(this.formLabel.height)) > 380) {
                this.$message({
                    showClose: true,
                    message: 'Y + 380',
                    type: 'error'
                });
                return false;
            }
            return true;
        },
        // 
        isEmpty(str) {
            if (str == null || str.trim() == "") {
                return true;
            }
            return false;
        },
        // 1
        isNumeric(str) {
            return /^\d+(\.\d+)?$/.test(str);
        }
    }
}
</script>

<style scoped>
/*  */
#operation {
    /*  250px */
    height: 100%;
    width: 250px;
    /*  */
    overflow-y: scroll;
    overflow-x: hidden;
    background-color: white;
    /*  flex  column */
    display: flex;
    flex-direction: column;
}

/*  */
#account {
    font-weight: bold;
    font-size: 18px;
    margin: 10px 5px 0px 15px;
}

/*  */
#search {
    margin: 10px auto 0;
}

/*  */
#switch {
    margin-top: 10px;
    position: relative;
    left: 110px;
}

/* / */
#info,
#edit_info {
    width: 220px;
    margin: 0px auto 0;
}

/*  */
#link {
    margin-bottom: 10px;
    margin-top: 10px;
    position: relative;
    left: 110px;
}

/*  */
#map-button {
    margin: 0px 0px 0px 15px;
}

/*  */
#map-button .displayFilter {
    margin: 0px 15px 0px 0px;
    width: 100px;
}

/*  */
#canvas {
    margin: 0 auto;
    background-color: white;
    border: 1px solid black;
}
</style>